import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OnboardingComponent } from './on-board/on-boarding/on-boarding.component';
import { LoginComponent } from './login/login/login.component';
import { AuthGuard } from './shared/auth-guard/auth.guard';
import { StudentsListComponent } from './on-board/student-list/student-list.component';
import { OnboardingFormComponent } from './on-board/on-boarding-form/on-boarding-form.component';
import { RegisterComponent } from './login/register/register.component';
const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'login' },
  { path: 'login', component: LoginComponent },
  {path: 'register',component: RegisterComponent},
  {
    path: 'onBoard', component: OnboardingComponent,
    children: [                          //<---- child components declared here
      {
        path: 'form',
        component: OnboardingFormComponent
      },

      {
        path: 'studentlist',
        component: StudentsListComponent
      }],
    canActivate: [AuthGuard]
  }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
