import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, BehaviorSubject } from 'rxjs';
import { catchError, tap, map, filter } from 'rxjs/operators';
import { IStudent } from '../../interfaces/student';
@Injectable({
  providedIn: 'root'
})
export class StudentService {
  private baseUrl: string = 'http://localhost:4200/api/students';
  private studentData$ = new BehaviorSubject<IStudent[]>(null);
  private StudentDataForEdit$ = new BehaviorSubject(null);

  getStudentDataForEdit(){
    return this.StudentDataForEdit$.asObservable().pipe();
  }
  sendStudentDataForEdit(value) {
    this.StudentDataForEdit$.next(value);
  }
  constructor(private httpClient: HttpClient) { }

  getStudent(id: number): Observable<IStudent> {
    const url = `${this.baseUrl}/${id}`;
    return this.httpClient.get<IStudent>(url).pipe(
      catchError(this.handleError)
    );
  }
  getStudentData() {
    return this.studentData$.asObservable();
  }
  sendStudentData() {
    let data = this.httpClient.get<IStudent[]>(this.baseUrl).subscribe(
      value => {
        this.studentData$.next(value);
      }
    );
  }
  getStudents(): Observable<IStudent[]> {
    return this.httpClient.get<IStudent[]>(this.baseUrl).pipe(
      catchError(this.handleError)
    );
  }
  getNextIdForStudent(): Observable<number> {
    return this.getStudents().pipe(
      map((students: IStudent[]) => students.reduce(function (prev, curr) {
        return (prev.id > curr.id) ? prev : curr;
      }).id + 1)
    );
  }
  addStudent(student: IStudent): Observable<IStudent> {
    try {
      console.log("add");
      console.log(student);
      return this.httpClient.post<IStudent>(this.baseUrl, student).pipe(
        catchError(this.handleError));
    } catch (ex) {
      console.log(ex.message);
    }
  }
  deleteStudent(id: number): Observable<IStudent> {
    try {
      const url = `${this.baseUrl}/${id}`;
      return this.httpClient.delete<IStudent>(url).pipe(

        catchError(this.handleError));
    } catch (ex) {
      console.log(ex.message);
    }
  }
  updateStudent(student: IStudent): Observable<IStudent> {
    try {
      
      console.log("edit");
      console.log(student);
      const url = `${this.baseUrl}/${student.id}`;
      return this.httpClient.put<IStudent>(url, student).pipe(
        catchError(this.handleError));
    } catch (ex) {
      console.log(ex.message);
    }
  }

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }
}
