import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../shared/services/auth/auth.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
/**
 * 
 * This component is used for registering user for access to application
 * @export
 * @class RegisterComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})

export class RegisterComponent implements OnInit {
  
  doesEmailExist:boolean;
  registerForm: FormGroup
  constructor(private toastr: ToastrService, private router: Router, private formBuilder: FormBuilder, private authService: AuthService) { }

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      username: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required]
    });
  }

  /**
   * method called when user clicks register after filling all the fields
   * If email already exists validation error appears
   *  After registering it navigates to login page
   * @param {any} form 
   * 
   * @memberOf RegisterComponent
   */
  onSubmit(form): void {
    this.authService.checkCredentials(this.registerForm.value).subscribe(value => {
      if (value != null && value.length > 0) {
       this.doesEmailExist=true;
       this.reset();
      }
      else {
        this.authService.registerUser(this.registerForm.value).subscribe(value => {
          this.router.navigateByUrl('/login');
          this.toastr.success("Registeration successful! Please try login now")
        });
      }
    });
  }
  
  /**
   * This method is used to reset form
   * 
   * 
   * @memberOf RegisterComponent
   */
  reset():void{
    this.registerForm.reset();
    this.registerForm.markAsPristine();
    this.registerForm.markAsUntouched();
  }
}
