import { Injectable } from '@angular/core';
import { IUser } from '../../interfaces/user';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, BehaviorSubject } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private baseUrl: string = 'http://localhost:4200/api/users';
  private loggedInUser$ = new BehaviorSubject(null);
  constructor(private httpClient: HttpClient) { }

  checkCredentials(userInfo: IUser): Observable<IUser[]> {
    return this.httpClient.get<IUser[]>(this.baseUrl).pipe(
      map((user: IUser[]) => user.filter(x => x.email == userInfo.email && x.password == userInfo.password)));
  }

  getLoggedInUser():IUser {
    return JSON.parse(localStorage.getItem('loggedInUser'));
  }
  public login(userInfo) {
    localStorage.setItem('token', "myToken");
    localStorage.setItem('loggedInUser', JSON.stringify(userInfo));
  }

  public isLoggedIn() {
    return localStorage.getItem('token') !== null;

  }

  registerUser(user: IUser): Observable<IUser> {
    try {
      this.getNextIdForUser().subscribe(value => {
        user.id = value;
      })
      return this.httpClient.post<IUser>(this.baseUrl, user).pipe(
        catchError(this.handleError)
      );
    } catch (ex) {
      console.log(ex.message);
    }
  }
  getNextIdForUser(): Observable<number> {
    return this.getUsers().pipe(
      map((user: IUser[]) => user.reduce(function (prev, curr) {
        return (prev.id > curr.id) ? prev : curr;
      }).id + 1)
    );
  }
  getUsers(): Observable<IUser[]> {
    return this.httpClient.get<IUser[]>(this.baseUrl).pipe(
    );
  }

  public logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('loggedInUser');
  }
  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }
}