import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, NgForm, FormGroup, AbstractControl, ValidatorFn } from '@angular/forms';
import { } from '@angular/forms';
import { Validators } from '@angular/forms';
import { StudentService } from '../../shared/services/student/student.service';
import { IStudent } from '../../shared/interfaces/student';
import { MatSnackBar } from '@angular/material';
import { ToastrService } from 'ngx-toastr';
/**
 * This component is used to display an on boarding form.
 * This component can be used to onboard , edit , view students through a form.
 * @export
 * @class OnboardingFormComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'app-on-boarding-form',
  templateUrl: './on-boarding-form.component.html',
  styleUrls: ['./on-boarding-form.component.css']
})
export class OnboardingFormComponent implements OnInit {

  onBoardForm: FormGroup;
  student: IStudent = null;
  students: IStudent[] = [];
  isDisabled: boolean = false;
  categories = ['Domestic', 'International'];

  /**
   * Creates an instance of OnboardingFormComponent.
   * @param {ToastrService} toastr 
   * @param {FormBuilder} fb 
   * @param {StudentService} studentService 
   * 
   * @memberOf OnboardingFormComponent
   */
  constructor(private toastr: ToastrService, private fb: FormBuilder, private studentService: StudentService) {

  }
  /**
   * Used to fetch data for edit and initialize form
   * @memberOf OnboardingFormComponent
   */
  ngOnInit() {
    this.fetchDataForEdit()
    this.initializeForm();
  }

  /**
   * Fetch data for edit if present
   * @memberOf OnboardingFormComponent
   */
  fetchDataForEdit(): void {
    this.studentService.getStudentDataForEdit().subscribe(value => {
      if (value != null) {
        this.student = value.student;
        this.isDisabled = value.disabled;
      }
    });
  }

  /**
   * Initialize form with edit data for edit operation if present 
   * else initialize with default values for add operation
   * @memberOf OnboardingFormComponent
   */
  initializeForm(): void {
    this.onBoardForm = this.fb.group({
      id: [this.student != null ? this.student.id : 0],
      studentName: [{ value: this.student != null ? this.student.studentName : '', disabled: this.isDisabled }, [Validators.required, Validators.maxLength(50)]],
      category: [{ value: this.student != null ? this.student.category : 'Domestic', disabled: this.isDisabled }, [Validators.required]],
      documents: this.fb.group({
        domicile: [{ value: this.student != null ? this.student.documents.domicile : false, disabled: this.isDisabled }, [Validators.pattern('true')]],
        birthCertificate: [{ value: this.student != null ? this.student.documents.birthCertificate : false, disabled: this.isDisabled }, [Validators.pattern('true')]],
        markSheets: [{ value: this.student != null ? this.student.documents.birthCertificate : false, disabled: this.isDisabled }, [Validators.pattern('true')]],
        policeClearance: [{ value: this.student != null ? this.student.documents.policeClearance : false, disabled: this.isDisabled }],
        passport: [{ value: this.student != null ? this.student.documents.passport : false, disabled: this.isDisabled }],
        declaration: [{ value: this.student != null ? this.student.documents.declaration : false, disabled: this.isDisabled }, [Validators.pattern('true')]]
      }),
      dob: [{ value: this.student != null ? this.student.dob : '', disabled: this.isDisabled }, [Validators.required, this.dateValidator()]],
      fatherName: [{ value: this.student != null ? this.student.fatherName : '', disabled: this.isDisabled }, [Validators.required, Validators.maxLength(50)]],
      motherName: [{ value: this.student != null ? this.student.motherName : '', disabled: this.isDisabled }, [Validators.required, Validators.maxLength(50)]],
      lastClassScore: [{ value: this.student != null ? this.student.lastClassScore : '', disabled: this.isDisabled }, [Validators.required]],
    });
    if (this.student != null)
      this.onSelectingCategory(this.student.category);
  }

  /**
   * method called when user clicks on submit button.
   * If id field is present it will edit else it will update.
   * @param {NgForm} form 
   * 
   * @memberOf OnboardingFormComponent
   */
  onSubmit(form: NgForm) {
    this.student = this.onBoardForm.value;
    if (this.student.id != 0 && this.student.id != null) {
      this.edit();
    }
    else {
      this.add();
    }
    this.reset();
  }

  /**
   * method used to edit student
   * @memberOf OnboardingFormComponent
   */
  edit(): void {
    this.studentService.updateStudent(this.student).subscribe(value => {
      this.toastr.success('Updated successfully');
      this.studentService.sendStudentData();
      this.student = null;
    });
  }

  /**
   * method used to add student
   * @memberOf OnboardingFormComponent
   */
  add(): void {
    this.studentService.getNextIdForStudent().subscribe(value => {
      this.student.id = value;
      this.studentService.addStudent(this.student).subscribe(value => {
        this.toastr.success('OnBoarded Sucessfully');
        this.studentService.sendStudentData();
        this.student = null;
      });
    })
  }
  /**
   * Used to reset the form after onboarding
   * @memberOf OnboardingFormComponent
   */
  reset(): void {
    this.onBoardForm.reset();
    this.onBoardForm.markAsPristine();
    this.onBoardForm.markAsUntouched();
  }

  /**
   * this method is used to change documents that are required depending upon category
   * @param {string} category 
   * 
   * @memberOf OnboardingFormComponent
   */
  onSelectingCategory(category: string): void {
    console.log(category);
    const policeClearanceControl = this.onBoardForm.get('documents.policeClearance');
    const passportControl = this.onBoardForm.get('documents.passport');

    if (category == "International") {
      policeClearanceControl.setValidators(Validators.pattern('true'));
      passportControl.setValidators(Validators.pattern('true'));
    }
    else {
      console.log("clearing validators");
      policeClearanceControl.clearValidators();
      passportControl.clearValidators();
    }
    policeClearanceControl.updateValueAndValidity();
    passportControl.updateValueAndValidity();
  }

  /**
   * This custom validator is used to perform date of birth validation
   * Student with age 3 years and above is allowed
   * @returns {ValidatorFn} 
   * 
   * @memberOf OnboardingFormComponent
   */
  dateValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      const dateStr = control.value;
      var dateEntered = new Date(dateStr);
      var currentDate = new Date();
      if (currentDate.getFullYear() - dateEntered.getFullYear() < 3) {
        return { "invalidDob": "Student must be of atleast 3 years;" }
      }
    }
  }
}
